import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';

// @mui

import { Card, Container, Grid, Box, Stack, Typography, Divider } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import GoogleMapReact from 'google-map-react';
// components
import Page from '../components/Page';
import CarFront from '../components/CarFront';
import Layout from '../layout'



// ----------------------------------------------------------------------

const PointComponent = () => <div style={{ width: 30, height: 30 }}><CarFront color={"red"} /></div>

CarLocation.propTypes = {
  lat: PropTypes.number,
  lng: PropTypes.number,
  center: PropTypes.object,
}
export default function CarLocation({ center, lat, lng }) {

  const defaultProps = {
    center: {
      lat: center?.lat||10.99835602,
      lng: center?.lng||77.01502627
    },
    zoom: 2
  };
  return (

    <div style={{ height: '100vh', width: '100%' }}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: "" }}
        defaultCenter={defaultProps.center}
        defaultZoom={defaultProps.zoom}
      >
        <PointComponent
          lat={lat!==undefined?lat:59.955413}
          lng={lng!==undefined?lng:30.337844}
        />
      </GoogleMapReact>
    </div>

  );
}
