import { useState, useEffect } from 'react';

// @mui

import { Card, Container, Grid, Box, Stack, Typography, Divider } from '@mui/material';
import { useTheme } from '@mui/material/styles';
// pusher
import io from 'socket.io-client';
// components
import Page from '../components/Page';
import CarTop from '../components/CarTop';
import Iconify from '../components/Iconify';
import ControlPanel from '../components/ControlPanel';

import FindingScreen from '../components/FindingScreen';
// hooks
import useResponsive from '../hooks/useResponsive';
import useAuth from '../hooks/useAuth';
import Layout from '../layout';
import axios from '../utils/axios';
import CarLocation from './CarLocation';
import { PUSHER_API, HOST_API } from '../config';

// ----------------------------------------------------------------------

export default function Home() {
  const theme = useTheme();
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('initializing...');
  const [description, setDescription] = useState('');
  const { user } = useAuth();
  const [action, setAction] = useState('');
  const [deviceStatus, setDeviceStatus] = useState();
  const [color, setColor] = useState(theme.palette.grey[500_48]);
  const [viewMap, setViewMap] = useState(false);
  const [motionTime,setMotionTime] =useState();
  const handleTurnOn = () => {
    setMessage("Turn on the car's engine....");
    handleDeviceControl('turnon');
  };
  const handleTurnOff = () => {
    setMessage("Turn off the car's engine....");
    handleDeviceControl('turnoff');
  };
  const handleLock = () => {
    setMessage("Lock the car's door....");
    handleDeviceControl('lock');
  };
  const handleUnlock = () => {
    setMessage("Unlock the car's door....");
    handleDeviceControl('unlock');
  };
  const handleLocation = () => {
    setViewMap(!viewMap);
  };
  const handleDeviceControl = (cmd) => {
    setLoading(true);
    setDescription('');
    axios
      .post(`/api/device/control/:${cmd}`)
      .then((res) => {
       
        if (res.data.success) {
          setAction(cmd);
          setDescription(`${res.data.action} was success`);


        } else if (res.data.err) {
          setAction('');
          if (typeof (res.data.err) === 'object')
            setDescription(`code = ${res.data.err.code}, host= ${res.data.err.address}, port =  ${res.data.err.port}`);
          if (typeof (res.data.err) === 'string')
            setDescription(res.data.err);

        }
        setTimeout(() => {
          setLoading(false);
        }, 3000);
      })
      .catch((res) => {
        setLoading(false);
      });
  };
  const isMobile = useResponsive('down', 'sm');
  useEffect(() => {
    if (user && user.phoneNumber) {
      const socket = io.connect(`${HOST_API}`);
      // socket.on('connect', () => console.log('connected'));
      socket.emit('logined', user);
	   
      socket.on('data-received', (data) => {
        console.log(data, ' received data');
        if (data.payload && data.from_client_id === user.phoneNumber && data.from === user.device.type) {
          const payload = JSON.parse(data.payload);
          console.log(payload, 'is payload data');
          const ts = new Date(data.ts);
          if(payload.motion === 1){
		   
            setMotionTime(`${ts.getHours()}:${ts.getMinutes()}:${ts.getSeconds()}`);
          }
		  if(payload.motion ===1 && payload.sta ===0){
			  setColor('red');
		  }
		  if(payload.sta ===1 ){
			setColor('green');
		  }
          setDeviceStatus({
            temp: payload.temp,
            hum: payload.hum,
            Lat: payload.Lat,
            Lon: payload.Lon,
            motion: payload.motion,
            light: payload.light,
            sta: payload.sta,

          });
        }
      });
      console.log('socket connected');
    }
  }, []);

  return (
    <Page title="Remote Car Control">
      <Layout />
      <Container sx={{ pt: { xs: 8, md: 11 } }}>
        {loading && <FindingScreen message={message} description={description} />}
        <Grid container spacing={3} sx={{ backgroundImage: 'url(images/back.svg)' }}>
          <Grid item xs={12} md={6} textAlign={'center'}>
            <CarTop sx={{ width: '50%', m: 'auto' }} device={user.device} color={color} action={action} status = {deviceStatus} />
            {isMobile && (
              <Stack gap={2} position={'absolute'} top={'15%'} left={'5%'}>
                <Stack gap={2} direction={'row'} justifyItems={'cneter'}>
                  <Iconify icon={'carbon:temperature-max'} width={30} height={30} />
                  <Typography>{deviceStatus?.temp || 'unreadable'}</Typography>
                </Stack>
                <Stack gap={2} direction={'row'} justifyItems={'cneter'}>
                  <Iconify icon={'carbon:humidity'} width={30} height={30} />
                  <Typography>{deviceStatus?.hum || 'unreadable'}</Typography>
                </Stack>

                <Stack gap={2} direction={'row'} justifyItems={'cneter'}>
                  {(deviceStatus && deviceStatus.light === 1 && (
                    <Iconify icon={'iconoir:light-bulb-on'} width={30} height={30} />
                  )) || <Iconify icon={'iconoir:light-bulb-off'} width={30} height={30} />}
                  <Typography>Light: {(deviceStatus && deviceStatus.light === 1 && 'ON') || 'OFF'}</Typography>
                </Stack>
                <Stack gap={2} direction={'row'} justifyItems={'cneter'}>
                  <Iconify icon={'bx:water'} width={30} height={30} />
                  <Typography>
                    {motionTime}
                  </Typography>
                </Stack>
              </Stack>
            )}
          </Grid>

          <Grid item xs={12} md={6}>
            <Stack gap={3} justifyContent={'space-between'}>
              {!isMobile && (
                <>
                  <Typography variant="h4" sx={{ mt: 3 }}>
                    Informations
                  </Typography>
                  <Divider />
                  <Box
                    sx={{
                      display: 'grid',
                      columnGap: 2,
                      rowGap: 3,
                      gridTemplateColumns: { xs: 'repeat(2, 1fr)', md: 'repeat(3, 1fr)' },
                    }}
                  >
                    <Card
                      sx={{ minHeight: 100, border: '1px solid', borderColor: 'grey.50048', p: 2, textAlign: 'center' }}
                    >
                      <Iconify icon={'carbon:temperature-max'} width={30} height={30} />
                      <Typography>{deviceStatus?.temp || 'unreadable'}</Typography>
                    </Card>
                    <Card
                      sx={{ minHeight: 100, border: '1px solid', borderColor: 'grey.50048', p: 2, textAlign: 'center' }}
                    >
                      <Iconify icon={'carbon:humidity'} width={30} height={30} />
                      <Typography>{deviceStatus?.hum || 'unreadable'}</Typography>
                    </Card>
                    <Card
                      sx={{ minHeight: 100, border: '1px solid', borderColor: 'grey.50048', p: 2, textAlign: 'center' }}
                    >
                      {(deviceStatus && deviceStatus.light === 1 && (
                        <Iconify icon={'iconoir:light-bulb-on'} width={30} height={30} />
                      )) || <Iconify icon={'iconoir:light-bulb-off'} width={30} height={30} />}
                      <Typography>Light: {(deviceStatus && deviceStatus.light === 1 && 'ON') || 'OFF'}</Typography>
                    </Card>
                  </Box>

                  <Typography variant="h4">Car Control</Typography>
                </>
              )}

              <Divider />
              <ControlPanel
                sx={{ mx: 'auto' }}
                handleLock={handleLock}
                handleTurnOff={handleTurnOff}
                handleTurnOn={handleTurnOn}
                handleUnlock={handleUnlock}
                handleLocation={handleLocation}
              />
            </Stack>
          </Grid>
          {viewMap && (
            <Grid item xs={12}>
              <Stack gap={3} justifyContent={'space-between'}>
                <CarLocation lat={deviceStatus?.Lat} lng={deviceStatus?.Lon} />
              </Stack>
            </Grid>
          )}
        </Grid>
      </Container>
    </Page>
  );
}
